
// 本文件为标签api
import { API_URL } from '@/constants/';
import axios from './axios';

export default {
  getAllType() {
    return axios.get(`${API_URL}/labels`).then((res) => ({
      data: res.data,
    }));
  },
  getType1() {
    return axios.get(`${API_URL}/labels/type1`).then((res) => ({
      data: res.data,
    }));
  },
  getType2(data) {
    return axios.get(`${API_URL}/labels/type2`, { params: data }).then((res) => ({
      data: res.data,
    }));
  },
  getType3(data) {
    return axios.get(`${API_URL}/labels/type3`, { params: data }).then((res) => ({
      data: res.data,
    }));
  },
  getType4(data) {
    return axios.get(`${API_URL}/labels/type4`, { params: data }).then((res) => ({
      data: res.data,
    }));
  },
  getType5(data) {
    return axios.get(`${API_URL}/labels/type5`, { params: data }).then((res) => ({
      data: res.data,
    }));
  },
};
