<template>
  <div class="platform">
    <el-form :model="trainForm" :inline="true" :rules="rules"
             ref="trainForm" label-width="125px">
      <el-form-item prop="label" label="选择待训练标签">
        <el-cascader v-model="trainForm.label" class="width-440" :props="cascaderProps">
        </el-cascader>
      </el-form-item>
      <el-form-item prop="fileList" label="导入训练数据">
        <el-upload
          :action="action"
          :limit="1"
          :before-upload="beforeAvatarUpload"
          :on-exceed="handleExceed"
          :before-remove="beforeRemove"
          :file-list="trainForm.fileList">
          <el-button size="small" type="primary" icon="el-icon-upload2">点击上传</el-button>
          <div slot="tip" class="el-upload__tip">只能上传 xls 或 xlsx 文件</div>
        </el-upload>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="trainingFn" :loading="loading">训练</el-button>
      </el-form-item>
    </el-form>
    <el-form :model="searchForm" label-width="125px" :inline="true">
      <el-form-item label="推理评价">
        <el-select v-model="searchForm.evaluation" class="width-220" clearable>
          <el-option v-for="(value, key) in evaluationObj" :key="key" :value="key" :label="value">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="推理结果">
        <el-select v-model="searchForm.result" class="width-220" clearable>
          <el-option v-for="(value, key) in resultObj" :key="key" :value="key" :label="value">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="searchFn" :loading="tableLoading">查询</el-button>
      </el-form-item>
    </el-form>
    <el-table
      :data="tableData" v-loading="tableLoading"
      style="width: 100%; margin-bottom: 22px;">
      <el-table-column type="index" :index="setIndexFn" label="序号" width="50">
      </el-table-column>
      <el-table-column prop="sentence" label="文本">
      </el-table-column>
      <el-table-column prop="result" label="推理结果" width="150">
      </el-table-column>
      <el-table-column prop="realResult" label="真实结果" width="150">
      </el-table-column>
      <el-table-column props="evaluation" label="推理评价" align="center" width="120">
        <template slot-scope="scope">
          <i :class="['el-icon-success', { 'is-active': scope.row.evaluation }]"></i>
          <i :class="['el-icon-error', { 'is-active': !scope.row.evaluation }]"></i>
        </template>
      </el-table-column>
      <el-table-column label="操作" width="100" align="center">
        <template slot-scope="scope">
          <el-tooltip effect="dark" content="更新语料" placement="top">
            <el-button type="text" icon="el-icon-s-order"
                       :disabled="scope.row.evaluation"
                       @click="tableUpdateFn(scope.row)"></el-button>
          </el-tooltip>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      @size-change="sizeChange"
      @current-change="currentChange"
      :current-page="pagination.current"
      :page-sizes="[10, 20, 50, 100]"
      :page-size="pagination.pageSize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="pagination.total">
    </el-pagination>
    <div class="dataCollection">
      <div class="left">训练情况一览</div>
      <div class="middle">
        <span v-for="(value, key) in collectionEnum" :key="key">
          {{value}}：{{trainingSet[key] && trainingSet[key].toFixed(2)}}
        </span>
      </div>
      <el-button class="right" type="primary"
                 :loading="onlineLoading" @click="onlineFn">一键上线</el-button>
    </div>
  </div>
</template>

<script>
import labels from '@/api/labels';
import train from '@/api/train';
import online from '@/api/online';

export default {
  data() {
    return {
      loading: false,
      tableLoading: false,
      onlineLoading: false,
      action: '/api/upload',
      realData: [],
      tableData: [],
      type: [
        'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet',
      ],
      extension: [
        'xls',
      ],
      cascaderProps: {
        lazy: true,
        checkStrictly: true,
        lazyLoad: this.lazyLoad,
      },
      trainForm: {
        label: [],
        fileList: [],
      },
      rules: {
        label: [{
          required: true,
          message: '请选择训练模型',
          trigger: 'change',
        }],
      },
      evaluationObj: {
        correct: '正确',
        error: '错误',
      },
      resultObj: {
        current: '当前训练标签',
        other: '其他',
      },
      searchForm: {
        result: '',
        evaluation: '',
      },
      pagination: {
        current: 1,
        pageSize: 10,
        total: 0,
      },
      collectionEnum: {
        correct: '准确总数',
        accuracy: '准确率',
        len: '文本总数',
        recall: '召回率',
        fpr: '误报率',
      },
      conversionEnum: {
        警情摘要: 'policeSummary',
        反馈内容: 'feedbackContent',
        '警情类别1(总队)': 'alertCategory1',
        '警情类别2(总队)': 'alertCategory2',
        '警情类别3(总队)': 'alertCategory3',
        '警情类别4(总队)': 'alertCategory4',
        result: 'result',
        reason: 'reason',
      },
      trainingSet: {},
    };
  },
  methods: {
    fetchType(level, code) {
      return new Promise((resolve) => {
        labels[`getType${level}`]({ key: code }).then(({ data }) => {
          resolve(data.data.map(({ name, key }) => ({
            value: name,
            label: name,
            leaf: level > 4,
            key,
          })));
        });
      });
    },
    async lazyLoad(node, resolve) {
      const { level, data } = node;
      if (level <= 4) {
        resolve(await this.fetchType(level + 1, data && data.key));
      }
      resolve([]);
    },
    beforeRemove(file) {
      if (file && file.status === 'success') {
        return this.$confirm(`确定移除 ${file.name}？`);
      }
      return true;
    },
    beforeAvatarUpload(file) {
      const extension = file.name.split('.').pop();
      const isType = this.type.includes(file.type) || this.extension.includes(extension);
      // const isSize = file.size / 1024 / 1024 < 2;
      if (!isType) this.$message.error('上传文件只能是 xls 或 xlxs 格式!');
      // if (!isSize) this.$message.error('上传文件大小不能超过 2MB!');
      return isType;
    },
    handleExceed() {
      this.$message.warning('当前限制选择 1 个文件，请移除当前文件后，再上传新文件');
    },
    setResultFn(object, deep, symbol) {
      if (!object[`alertCategory${deep + 1}`]) return object[`alertCategory${deep}`];
      return `${object[`alertCategory${deep}`]}${symbol}${this.setResultFn(object, deep + 1, symbol)}`;
    },
    setTableDataFn(data) {
      const arr = [];
      Object.keys(data).forEach((key) => {
        Object.keys((data[key])).forEach((index) => {
          if (!arr[index]) arr[index] = {};
          arr[index][this.conversionEnum[key]] = data[key][index];
        });
      });
      // realData
      this.tableData = arr.map(({
        policeSummary, feedbackContent, ...item
      }) => {
        const obj = item;
        obj.sentence = `${policeSummary}${feedbackContent}`;
        obj.realResult = `${this.setResultFn(item, 1, '_')}`;
        obj.evaluation = obj.realResult === obj.result;
        return obj;
      });
      this.realData = this.tableData;
      this.pagination.total = arr.length;
    },
    trainingFn() {
      this.$refs.trainForm.validate((valid) => {
        if (valid) {
          this.loading = true;
          train.train({
            label: this.trainForm.label.join('_'),
          }).then(({ data: { data, indict } }) => {
            this.loading = false;
            this.trainingSet = indict;
            this.setTableDataFn(JSON.parse(data));
          }).catch(() => {
            this.loading = false;
          });
        }
      });
    },
    searchFn() {
      this.tableLoading = true;
      this.pagination.current = 1;
      const { evaluation: assay, result: effect } = this.searchForm;
      const label = this.trainForm.label.join('_');
      let evalFlag = null;
      let effectFlag = null;
      if (assay) evalFlag = assay === 'correct';
      if (effect) effectFlag = effect === 'current';
      this.tableData = this.realData.filter(({ evaluation, result }) => ((evalFlag !== null
        && evaluation === evalFlag) || evalFlag === null)
        && ((effectFlag !== null && ((effectFlag && result === label)
          || (!effectFlag && result !== label))) || effectFlag === null));
      this.pagination.total = this.tableData.length;
      this.tableLoading = false;
    },
    setIndexFn(index) {
      const { current, pageSize } = this.pagination;
      return (current - 1) * pageSize + index + 1;
    },
    tableUpdateFn({ reason, sentence }) {
      this.$router.push({
        path: '/learningPlatform/semanticAnnotation',
        query: {
          label: this.trainForm.label.join('_'),
          sentence,
          reason,
        },
      });
    },
    sizeChange(val) {
      this.pagination.pageSize = val;
    },
    currentChange(val) {
      this.pagination.current = val;
    },
    onlineFn() {
      this.onlineLoading = true;
      online.online().then(() => {
        this.onlineLoading = false;
        this.$message.success('上线成功');
      }).catch(() => {
        this.onlineLoading = false;
        this.$message.error('上线失败');
      });
    },
  },
};
</script>

